.product
{
    width: 250px;
    height: 390px;
    display: flex;
    flex-direction: column;
    /* justify-content: space-between; */
    flex-wrap: wrap;
    align-items: center;
    margin: 10px;
    padding: 10px;
    border-radius: 10px;
    box-shadow: 0px 0px 8px 1px grey;
    margin: 0px 20px 50px 10px;
}

.product:hover
{
    transform: scale(1.1);
    transition: 0.3s;
    box-shadow: 0px 0px 30px 5px grey;
}


.photo
{
    /* height: 130px; */
    height: 45%;
    width: 53%;
    /* object-fit: cover; */
    border-radius: 1px solid red;
    margin: 0px;
}

.pp
{
    height: 100px;
    margin-bottom: 30px;
    width: 80%;
}

.desc
{
    color: rgb(70, 70, 70);
    font-size: 13px;
}

.title
{
    font-weight: 500;
}
.addremove
{
    height: 40px;
    width: 150px;
    border: none;
    border-radius: 20px;
    background-color: white;
    color: rgb(0, 0, 41);
    font-weight: bold;
    border: 3px solid rgb(0, 0, 41);   
}

.addremove:hover
{
    background-color: rgb(0, 0, 41);
    color: white;
}

.flexbtn
{
    width: 250px;
    margin: 30px 0px 0px 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    z-index: 1;
}


.price 
{
    font-weight: bold;
    color: rgb(0, 158, 0);
}
