.spinner {
   width: 48px;
   height: 48px;
   border: 5px solid rgb(211, 211, 211);
   border-bottom-color: #FF3D00;
   border-radius: 50%;
   display: inline-block;
   box-sizing: border-box;
   animation: rotation .7s linear infinite;
   position: fixed;
   margin: 30vh 48vw;
   }

   @keyframes rotation {
   0% {
       transform: rotate(0deg);
   }
   100% {
       transform: rotate(360deg);
   }
   } 