header 
{
    height: 80px;
    width: 100vw;
    background-color: rgb(0, 0, 41);
    position: fixed;
    top: 0;
    z-index: 2;
}

.logo
{
    height: 50px;
    margin: 0px 0px 0px 20px;
    width: 170px;
    cursor: pointer;
    /* object-fit: cover; */
}

.short 
{
    height: 77px;
    width: 60vw;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.flex 
{
    display: flex;
    align-items: center;
    margin: 0px 0px 0px 0px;
}
.home
{
    font-size: 18px;
    margin: 0px 50px 0px 0px;
    font-weight: 500;
    cursor: pointer;
    color: white;
    text-decoration: none;

}
.icon
{
    margin: 5px 20px 0px 0px;
    font-size: 25px;
    cursor: pointer;
    text-decoration: none;
    color: white;
}

.none
{
    color: white;
    text-decoration: none;
}

.pop
{
    position: fixed;
    font-size: 13px;
    height: 20px;
    width: 20px;
    background-color: green;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 20px;
    left: 78vw;
    animation: bounce .8s infinite ease;
}

@keyframes bounce
{
    0%
    {
        top: 20px;
        left: 78vw;
    }
    50%
    {
        top: 17px;
    }
    100%
    {
        top: 19px;
        left: 78vw;
    }
}

